import { Component, OnInit, Inject } from '@angular/core';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material';
import { ItemsService } from '../items.service';
import { NotifierService } from 'angular-notifier';

@Component({
  selector: 'app-delete-items',
  templateUrl: './delete-items.component.html',
  styleUrls: ['./delete-items.component.scss']
})
export class DeleteItemsComponent implements OnInit {

  private notifier: NotifierService;
  constructor(public matDialogRef: MatDialogRef<DeleteItemsComponent>,
    @Inject(MAT_DIALOG_DATA) public data: any, public itemService: ItemsService,
    notifierService: NotifierService) {
    this.notifier = notifierService;
  }

  ngOnInit() {
  }
  deleteItems() {
    this.data.modifiedBy=localStorage.getItem('user');
    this.itemService.deleteItems(this.data).subscribe((date) => {
      this.notifier.notify('success', 'Deleted Successfully');
      this.matDialogRef.close();
    }, (err) => {
      this.notifier.notify('error', 'Not Delete');
    })
  }

  cancel() {
    this.matDialogRef.close();
  }
}
