import { Component, OnInit, Inject } from '@angular/core';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material';
import { Items } from '../items';
import { ItemsService } from '../items.service';
import { NotifierService } from 'angular-notifier';
import { MasterService } from '../../master/master.service';
import { FormBuilder, Validators } from '@angular/forms';
import { ValidationService } from '../../validation/validation-service';
import { ItemTypeMaster } from '../../master/item-type-master/item-type-master';
import { UnitMaster } from '../../master/unit-master/unit-master';
import { ItemCategoryMaster } from '../../master/item-category-master/item-category-master';

@Component({
  selector: 'app-edit-items',
  templateUrl: './edit-items.component.html',
  styleUrls: ['./edit-items.component.scss']
})
export class EditItemsComponent implements OnInit {
  userForm: any;
  public items: Items;
  private notifier: NotifierService;
  public itemTypeMasterList: ItemTypeMaster[];
  public unitMasterList: UnitMaster[];
  public itemCategoryList: ItemCategoryMaster[];
  public perQtyWeight = 0;
  constructor(public itemService: ItemsService, public matDialogRef: MatDialogRef<EditItemsComponent>,
    @Inject(MAT_DIALOG_DATA) public data: Items, notifierService: NotifierService, public masterService: MasterService,
    private formBuilder: FormBuilder) {
    this.notifier = notifierService;
    this.userForm = this.formBuilder.group({
      'itemName': ['', [Validators.required, ValidationService.nameValidation]],
      'minWeightReq': ['', [Validators.required, ValidationService.numberValidator]],
      'gst': ['', [Validators.required, ValidationService.numberValidator]],
      'unit': ['', [Validators.required]],
      'itemType': ['', [Validators.required]],
      'itemCategory': ['', [Validators.required, ValidationService.nameValidation]],
      'perQtyWeight': ['', [Validators.required, ValidationService.numberValidator]],
      'status': ['', [Validators.required]],
    });
  }

  ngOnInit() {
    console.log('dataaaaa', this.data);
    this.items = new Items();
    this.getAllItemTypeMaster();
    this.getAllUnitMaster();
    this.getItemCategoryMaster();
  }

  getAllItemTypeMaster() {
    this.masterService.getItemTypeMaster().subscribe((data) => {
      this.itemTypeMasterList = data;
    }, (err) => {
      console.log(err);
    });
  }

  getAllUnitMaster() {
    this.masterService.getAllUnitMaster().subscribe((data) => {
      this.unitMasterList = data;
    }, (err) => {
      console.log(err);
    });
  }

  getItemCategoryMaster() {
    this.masterService.getItemCategoryMaster().subscribe((data) => {
      this.itemCategoryList = data;
    }, (err) => {
      console.log(err);
    });
  }

  UpdateItems() {
    this.itemTypeMasterList.forEach(res => {
      if (this.data.itemType === res.itemType) {
        this.data.stockType = res.stockType;
      }
    });
    this.data.modifiedBy = localStorage.getItem('user');
    console.log('dataaaaa', this.data);
    this.itemService.UpdateItems(this.data).subscribe((data) => {
      this.notifier.notify('success', 'Updated Successfully');
      this.matDialogRef.close();
    }, (err) => {
      this.notifier.notify('error', 'Not Update');
    });
  }

  cancel() {
    this.matDialogRef.close();
  }

}
