import { Component, OnInit, ViewChild } from '@angular/core';
import { MatTableDataSource, MatPaginator, MatSort } from '@angular/material';
import { MatDialog } from '@angular/material';
import { HttpClient } from '@angular/common/http';
import { AddItemsComponent } from './add-items/add-items.component';
import { EditItemsComponent } from './edit-items/edit-items.component';
import { DeleteItemsComponent } from './delete-items/delete-items.component';
import { Items } from './items';
import { ItemsService } from './items.service';
import { MasterService } from '../master/master.service';
import { Master } from '../master/master';

@Component({
  selector: 'app-items',
  templateUrl: './items.component.html',
  styleUrls: ['./items.component.scss']
})
export class ItemsComponent implements OnInit {

  displayedColumns: String[] = ['itemName', 'itemType', 'unit', 'gst', 'itemCategory', 'minWeightReq', 'status' , 'actions'];
  dataSource;
  public items: Items;
  constructor(public httpClient: HttpClient, public matDialog: MatDialog, public itemsService: ItemsService,
  ) { }

  @ViewChild(MatPaginator) paginator: MatPaginator;
  @ViewChild(MatSort) sort: MatSort;

  applyFilter(filterValue: String) {
    this.dataSource.filter = filterValue.trim().toLowerCase();
    if (this.dataSource.paginator) {
      this.dataSource.paginator.firstPage();
    }
  }

  ngOnInit() {
    this.items = new Items();
    this.getAllItems();
  }

  getAllItems() {
    this.itemsService.getAllItems().subscribe((data) => {
      this.dataSource = new MatTableDataSource(data);
      this.dataSource.paginator = this.paginator;
      this.dataSource.sort = this.sort;
    }, (err) => {
      console.log(err);
    });
  }

  addItems() {
    const matDialogRef = this.matDialog.open(AddItemsComponent, {
    });
    matDialogRef.afterClosed().subscribe((result) => {
      this.getAllItems();
    });
  }

  editItems(i: number, _id: String, itemName: String, itemType: String, unit: String, gst: number, itemCategory: String,
    minWeightReq: number, perQtyWeight: number, status: string) {
    const matDialogRef = this.matDialog.open(EditItemsComponent, {
      data: {
        _id: _id, itemName: itemName, itemType: itemType, unit: unit, gst: gst, itemCategory: itemCategory,
        minWeightReq: minWeightReq, perQtyWeight: perQtyWeight, status: status
      }
    });
    matDialogRef.afterClosed().subscribe((result) => {
      this.getAllItems();
    });
  }

  deleteItems(i: number, _id: String, itemName: String, quantity: number, unit: String, gst: number, minWeightReq: number) {
    const matDialogRef = this.matDialog.open(DeleteItemsComponent, {
      data: { _id: _id, itemName: itemName, quantity: quantity, unit: unit, gst: gst, minWeightReq: minWeightReq }
    });
    matDialogRef.afterClosed().subscribe((result) => {
      this.getAllItems();
    });
  }

  refresh() {
    this.getAllItems();
  }
}
