import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { ItemsComponent } from './items.component';
import { AddItemsComponent } from './add-items/add-items.component';
import { EditItemsComponent } from './edit-items/edit-items.component';
import { DeleteItemsComponent } from './delete-items/delete-items.component';
import {
  MatButtonModule, MatSortModule, MatPaginatorModule, MatInputModule, MatIconModule,
  MatAutocompleteModule, MatDialogModule, MatTableModule, MatSelectModule, MatTooltipModule
} from '@angular/material';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { HttpClientModule } from '@angular/common/http';
import { HttpModule } from '@angular/http';
import { ItemsRoutingModule } from './items-routing.module';
import { NotifierModule, NotifierOptions } from 'angular-notifier';
import { MasterService } from '../master/master.service';
import { CUSTOM_ELEMENTS_SCHEMA } from '@angular/core';
import { FoundryModule } from '../foundry.module';
const customNotifierOptions: NotifierOptions = {
  position: {
    horizontal: {
      position: 'middle',
      distance: 12
    },
    vertical: {
      position: 'top',
      distance: 50,
      gap: 10
    }
  },
  theme: 'material',
  behaviour: {
    autoHide: 1000,
    onClick: 'hide',
    onMouseover: 'pauseAutoHide',
    showDismissButton: true,
    stacking: 4
  },
  animations: {
    enabled: true,
    show: {
      preset: 'slide',
      speed: 300,
      easing: 'ease'
    },
    hide: {
      preset: 'fade',
      speed: 300,
      easing: 'ease',
      offset: 50
    },
    shift: {
      speed: 300,
      easing: 'ease'
    },
    overlap: 10
  }
};
@NgModule({
  declarations: [ItemsComponent, AddItemsComponent, EditItemsComponent, DeleteItemsComponent],
  imports: [
    CommonModule, MatButtonModule, MatSortModule, MatPaginatorModule, MatInputModule, MatIconModule,
    MatAutocompleteModule, MatDialogModule, FormsModule, ReactiveFormsModule, MatSelectModule, MatTooltipModule,
    HttpClientModule, HttpModule, ItemsRoutingModule, MatTableModule, NotifierModule.withConfig(customNotifierOptions),FoundryModule
  ],
  entryComponents: [AddItemsComponent, EditItemsComponent, DeleteItemsComponent],
  providers: [MasterService],
  schemas: [CUSTOM_ELEMENTS_SCHEMA]
})
export class ItemsModule { }
